.credits-container {
    width: 100%;
    padding: 70px 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .credits_item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    word-break: break-word;
    padding: 0 12px;
  }
  
  .credits_item-container:not(:last-child) {
    margin-bottom: 30px;
  }
  
  .credits_item-title {
    font-size: clamp(16px, 5vw, 24px);
    margin-bottom: 10px;
    letter-spacing: 1px;
  }
  
  .credits_item-name {
    font-size: clamp(20px, 6vw, 32px);
    font-weight: 500;
    letter-spacing: 2px;
  }
  